
.toUpdateWrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}
.toUpdateBox {
  position: absolute;
  width: 21.2rem;
  background-color: transparent;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.updateBg {
  width: 100%;
  height: 11.2rem;
  background: url("http://hly.1000da.com.cn/assets/img/toUpdateBg.png") no-repeat center center;
  background-size: cover;
  border-radius: 0.8rem 0.8rem 0 0;
}
.updatePlane {
  width: 4.12rem;
  height: 8.4rem;
  background: url("http://hly.1000da.com.cn/assets/img/plane.png") no-repeat;
  background-size: 100% 100%;
  position: absolute;
  top: -2.72rem;
  left: 50%;
  margin-left: -2.04rem;
  animation: rotatePlane 2s infinite;
  -moz-animation: rotatePlane 2s infinite;
  -webkit-animation: rotatePlane 2s infinite;
  -o-animation: rotatePlane 2s infinite;
}
.updatePlane.active {
  animation: myfirst 2s;
  -moz-animation: myfirst 2s;
  -webkit-animation: myfirst 2s;
  -o-animation: myfirst 2s;
}
@-webkit-keyframes rotatePlane {
50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}
@keyframes rotatePlane {
50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}
@-webkit-keyframes myfirst {
30% {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
}
100% {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
}
}
@keyframes myfirst {
30% {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
}
100% {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
}
}
.updateBg > i {
  position: absolute;
  width: 1.88rem;
  height: 1.88rem;
  right: 0.8rem;
  top: -3.6rem;
  background: url("assets/img/home/closeUpdate.png") no-repeat;
  background-size: 100% 100%;
}
.toUpdateContent {
  padding: 0.4rem 1.4rem 1.2rem;
  background-color: #fff;
  margin-top: -1px;
  border-radius: 0 0 0.8rem 0.8rem;
}
.aboutEdition {
  font-size: 1.12rem;
  line-height: 2rem;
  font-weight: bold;
  color: #333;
}
.aboutEdition > span {
  display: block;
}
.newContent {
  margin-top: 0.8rem;
  font-size: 1.12rem;
  line-height: 2rem;
  color: #666;
}
.newContent > * {
  display: block;
}
.toUpdateContent > a {
  display: block;
  margin-top: 2rem;
  background-color: #5e9fff;
  color: #ffffff;
  font: 1rem/2.66rem "微软雅黑";
  text-align: center;
}



